header {
  border-bottom: 3px solid var(--accent-color);
}

header > .container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.nav__list {
  gap: 80px;
  display: flex;
}

.nav__item {
  list-style: none;
}

.nav__btn {
  color: var(--accent-color);
  text-transform: uppercase;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  font-family: Courier New, Courier, monospace;
  font-size: 23px;
  font-weight: 900;
  transition: color .25s;
}

.nav__btn:hover {
  color: teal;
}

.logo__icon, .cart__icon {
  background-color: var(--accent-color);
  margin: 15px;
}

.exit-link__wrapper {
  margin-top: 15px;
  position: absolute;
}

.title {
  text-align: center;
  text-transform: uppercase;
  padding-top: 20px;
  font-size: 50px;
}

.products__list {
  flex-wrap: wrap;
  gap: 20px;
  margin: 0 auto;
  display: flex;
}

.product__item {
  width: calc(33.3333% - 13.3333px);
  border-radius: 5px;
  list-style: none;
}

.product__item--img {
  margin-bottom: 15px;
}

.product__item--title {
  text-transform: uppercase;
  margin-bottom: 15px;
  margin-left: 15px;
  text-decoration: none;
}

.product__item--wrapper {
  outline: 1px solid rgba(255, 255, 255, .3);
  position: relative;
}

.product__item--price {
  color: red;
  margin-left: 15px;
  padding-bottom: 15px;
  font-size: 20px;
}

.product__item--btn {
  cursor: pointer;
  background-color: var(--accent-color);
  border: none;
  border-radius: 50%;
  position: absolute;
  bottom: 23px;
  right: 20px;
}

.product__item--btn:hover {
  background-color: #fff;
}

